import BbwzConstDefine from "../data/BbwzConstDefine";

const {ccclass, property} = cc._decorator;

// 弹窗 规则界面
@ccclass
export default class BbwzRulePop extends cc.Component {
    private maskNode: cc.Node;
    private contentNode: cc.Node;
    private scroll: cc.ScrollView;
    private animComp: any;

    protected onLoad(){
        this.node.setContentSize(cc.Canvas.instance.node.getContentSize());     // 适配
        this.maskNode = this.node.getChildByName("mask");
        this.contentNode = this.node.getChildByName("content");
        this.animComp = Global.UIHelper.addAnimComp(this.node, this.contentNode, this.maskNode);
        
        this.scroll = this.contentNode.getChildByName("scrollview").getComponent(cc.ScrollView);
        BbwzConstDefine.addCommonClick(this.contentNode, "button_close", this.onCloseClick, this);
        BbwzConstDefine.addCommonClick(this.node, "mask", this.onCloseClick, this, cc.Button.Transition.NONE);
    }
    
    protected onEnable(){
        this.scroll.scrollToTop(0);
        this.animComp.doPopupOpenAnim();
    }

    private onCloseClick(){
        BbwzConstDefine.playBtnSound();
        this.animComp.doPopupCloseAnim(()=>{
            this.node.active = false;
        });
    }
}
